.card{
    position: relative;
    background-color: aliceblue;
    width: 600px;
    padding: 20px;
    max-width: 85vw;
    height: 50vh;
    box-shadow: 0px 10px 20px 0px rgba(0,0,0,0.1) ;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
}
.tindercards_cardcontainer{
    display: flex;
    justify-content: center;
    margin-top: 5vh;
}
.swipe{
    position: absolute;
}
.card h3{
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: white;
}